<template>
  <ul :style="{'width':width||'50%','height':height||'auto','backgroundColor':bgColor||'#fff','border':`0.01rem solid ${borderColor||'#ccc'}`}">
    <li v-for="(item,index) in data" :key="index" :style="{'backgroundColor':`${value===item.name?itemBgColor:''}`,'height':itemHeight||'0.7rem','justifyContent':alignPosition,'paddingLeft':indent}" @click="sendSelect(item.name)" class="content">
      <i v-if="item.icon&&item.icon!==''" :class="`${item.icon||''}`"></i><span :style="{'marginLeft':itemSpacing||'0','fontSize':fontSize||'0.24rem'}">{{item.name}}</span>
    </li>
  </ul>
</template>
<script>
export default {
  name: "jc-select",
  model: {
    prop: "value",
    event: "sendOption"
  },
  props: {
    data: {
      type: Array,
      require: true
    },
    value: {
      type: String,
      require: true
    },
    width: {}, // 选择框宽度: '2rem'
    height: {}, // 选择框高度: '4rem'
    bgColor: {}, // 选择框背景色: '#fff'
    fontSize: {}, // 字体大小: '0.24rem'
    borderColor: {}, // 选择框外边框色: '#ccc'
    itemBgColor: {
      // 内部元素选中时的背景色: '#fff'
      default() {
        return "#edf5ff";
      }
    },
    itemHeight: {}, // 内部元素高度: '0.7rem'
    itemSpacing: {}, // 内部元素的内部间距: '0.2rem'
    align: {}, // 内部元素的内容位置: 'left' || 'center' || 'right'
    indent: {
      // 内部元素偏移量
      default() {
        return "0.2rem";
      }
    }
  },
  methods: {
    sendSelect(val) {
      this.$emit("sendOption", val);
      this.$emit("close");
    }
  },
  computed: {
    alignPosition() {
      let pos = this.align;
      if (pos === "left") {
        pos = "flex-start";
      } else if (pos === "right") {
        pos = "flex-end";
      } else {
        pos = "center";
      }
      return pos;
    }
  }
};
</script>
<style lang="scss" scoped>
ul {
  border-radius: 0.1rem;
  padding: 0.1rem 0;
  margin: 0 auto;
  li {
    width: "100%";
    display: flex;
    align-items: center;
    padding-right: 0.2rem;
  }
}
</style>



